    //start band
    // rebar_value: "20",
    // band_name: "Enter name",
    // band_height: "0",
    // bottom_elevation: "0",
    // top_elevation: "",
    // band_material: "",
    // band_material_unit: "",
    // select_wall_type: "",
    // band_rebar_material: "",
    // band_fill_material: "",
    // band_rebar_overlap: "0",
    // tota_rebar_band_length: "",
    // total_rebar_band_piece: "",
    // rebar_quantity_per_space: "",
    // total_rebar_lf: "0",
    // total_area_grouted: "",
