    //vertical rebar and fill
    grout_fill_material: "",
    rebar_spacing: "0",
    additional_spacing: "0",
    total_spaces_filled: "0",
    rebar_lift_spaces: "",
    total_lifts: "",
    vertical_rebar_overlap: "",
    rebar_lf_pr_space: "",
