    lft_rebar_per_ton: "",
    vertical_total_rebar_tons: "",
    vertical_rebar_positioner: "",
    vertical_postioner_per_total: "",
    vertical_postioner_other_total: "",
    vertical_grout_material: "",
    vertical_grouted_area: "",
    remaining_area: "",
