      const calculateTotalCubicArea = calculationCubicArea(
        updatedFormData,
        calculateTotalSquareArea
      );
      const calculateWallSquareUnits = calculationWallSquareUnit(
        updatedFormData,
        calculateTotalSquareArea
      );
      // const calculateWallCubicUnits = calculationWallCubicUnit(updatedFormData, calculateTotalCubicArea);
