    // Create a copy of formDataLength.lines without the corresponding new row
    const updatedLines = formData.additionalDatas.filter(
      (_, index) => index !== rowIndex
    );
    // finalCalculations(formDataLength);
    setFormData((prevState) => ({
      ...prevState,
      lines: updatedLines,
    }));
    // Run calculations after updating the state
    // finalCalculations({
    //     ...formDataLength,
    //     lines: updatedLines,
