import { useEffect, useState } from "react";
import { createPortal } from "react-dom";
import Draggable from "react-draggable";
import { routes } from "../Constants";
import { toast} from "react-toastify"

function PerimeterPopup({ setDisplay }) {
    const [trade, setTrade] = useState([])
    const [wallMaterials, setWallMaterials] = useState([]);
    const [additionalMaterials, setAdditionalMaterials] = useState([]);
    // const [selectedId, setSelectedId] = useState('')
     // Add a state to store form data
     const [formDataPerimeter, setformDataPerimeter] = useState({
        name: "",
        main_material: "",
        perimeter_width: "",
        perimeter_material: "",
        perimeter: "",
        additional_material_name: "",
        additional_material_qty: "",
        note: "",
        // Add other form fields here...
      });
     // Update the form inputs to capture user input and update formData
     const handleInputChange = (event) => {
        const { name, value } = event.target;
        if (name === "main_material") {
          // Handle changes for main material
          setformDataPerimeter({
            ...formDataPerimeter,
            [name]: value,
          });
        } else if (name.startsWith("additional_material[")) {
          // Handle changes for additional materials
          const index = Number(name.match(/\d+/)[0]); // Extract the index from the name attribute
          const updatedMaterials = [...additionalMaterials];
          updatedMaterials[index] = value;
          setAdditionalMaterials(updatedMaterials);
          
          // Update the formDataCount with the new additional materials array
          setformDataPerimeter({
            ...formDataPerimeter,
            additional_material: updatedMaterials,
          });
        } else {
          // Handle changes for other form fields
          setformDataPerimeter({
            ...formDataPerimeter,
            [name]: value,
          });
        }
      };
       // Add a function to handle form submission and save data to local storage
    const handleFormSubmit = () => {
        // Save formDataPerimeter to local storage (convert it to a JSON string before saving)
        localStorage.setItem("savedformDataPerimeter", JSON.stringify(formDataPerimeter));
        const savedData = localStorage.getItem("savedformDataPerimeter");
        if (savedData === JSON.stringify(formDataPerimeter)) {
            // Data was successfully saved
            toast.success('Data Saved Successfully!', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: 0,
            });
        } else {
            // There was an issue saving the data
            toast.warning('Something went wrong. Please, try again!', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: 0,
            }); 
        }
        // You can do other actions here if needed after saving the data.
        // For example, close the popup or navigate to another page.
        setDisplay(false);
        };
        const handleAddClick = () => {
            setAdditionalMaterials([...additionalMaterials, ""]);
          };
          const handleDeleteClick = () => {
            if (additionalMaterials.length > 1) {
              const updatedMaterials = [...additionalMaterials];
              updatedMaterials.pop();
              setAdditionalMaterials(updatedMaterials);
            }
          };
    useEffect(() => {
        // Check if data is saved in local storage
        const savedData = localStorage.getItem("savedformDataPerimeter");
        if (savedData) {
            // If data exists in local storage, you can parse the JSON string back to an object
            const formDataPerimeterFromLocalStorage = JSON.parse(savedData);
            console.log("Data is saved:", formDataPerimeterFromLocalStorage);
            // Update the formDataPerimeter state with the saved data
            setformDataPerimeter(formDataPerimeterFromLocalStorage);
            if (formDataPerimeterFromLocalStorage.additional_material) {
                // If there are additional materials, set them to the additionalMaterials state
                setAdditionalMaterials(formDataPerimeterFromLocalStorage.additional_material);
            } else {
                // If no additional materials, initialize the additionalMaterials state as an empty array
                setAdditionalMaterials([]);
            }
        } else {
          console.log("Data is not saved yet.");
        }
        const fetchTradeData = async () => {
          try {
            const response = await fetch('/api/materials/divisions', {
              method: 'GET',
              headers: {
                Authorization: `Bearer ${routes.__token}`,
              },
            });
    
            if (!response.ok) {
              // Handle error if the response is not OK (status code 2xx)
              // Example: console.log('Error fetching trade data');
              return;
            }
    
            const data = await response.json();
            setTrade(data);
          } catch (error) {
            // Handle error if there's an issue with the API call
            // Example: console.error('API call failed:', error);
          }
        };
    
        fetchTradeData();
        const fetchWallMaterials = async () => {
            try {
              const response = await fetch('/api/materials', {
                method: 'GET',
                headers: {
                  Authorization: `Bearer ${routes.__token}`,
                },
              });
      
              if (!response.ok) {
                // Handle error if the response is not OK (status code 2xx)
                // Example: console.log('Error fetching wall materials data');
                return;
              }
      
              const data = await response.json();
              setWallMaterials(data);
            } catch (error) {
              // Handle error if there's an issue with the API call
              // Example: console.error('API call failed:', error);
            }
          };
          fetchWallMaterials();
      }, []);
    return (
        createPortal(
            <Draggable>
                <div className="popup__count">
                    <div className="modal-header">
                    Perimeter Takeoff
                        <span className="close-btn" onClick={() => { setDisplay(false); }}>&times;</span>
                    </div>
                    <form className="modal-body" onSubmit={handleFormSubmit}>
                        <label className="label">Takeoff Name</label>
                        <input 
                            type="text" 
                            placeholder="Takeoff Name" 
                            name="name" 
                            value={formDataPerimeter.name}
                            onChange={handleInputChange}
                        />
                        <label className="label">Select Trade</label>
                        <select 
                            name="trade"
                            value={formDataPerimeter.trade}
                            onChange={handleInputChange}
                        >
                            <option value="" hidden="">
                            -- Select Trade -- 
                            </option>
                            {trade.map((tradeItem) => (
                                <option key={tradeItem.id} value={JSON.stringify(tradeItem)}>
                                {tradeItem.name}
                                </option>
                            ))}
                        </select>
                        <label className="label text-center fw-bold">Main Material</label>
                        <div className="form-row">
                            <select 
                                name="main_material"
                                value={formDataPerimeter.main_material}
                                onChange={handleInputChange}
                            >
                            <option value="" hidden="">
                               -- Select Material --
                            </option>
                            {wallMaterials.map((material) => (
                                <option key={material.id} value={JSON.stringify(material)}>
                                    {material.name}
                                </option>
                            ))}
                            </select>
                            <input
                            name="perimeter_width"
                            type="number"
                            step="0.0001"
                            placeholder="Perimeter Width (Inch)"
                            value={formDataPerimeter.perimeter_width}
                            onChange={handleInputChange}
                            />
                        </div>
                        <label className="label text-center fw-bold">Perimeter Material</label>
                        <div className="form-row">
                            <select 
                                name="perimeter_material"
                                value={formDataPerimeter.perimeter_material}
                                onChange={handleInputChange}
                            >
                            <option value="" hidden="">
                                -- Select Material --
                            </option>
                            {wallMaterials.map((material) => (
                                <option key={material.id} value={JSON.stringify(material)}>
                                    {material.name}
                                </option>
                            ))}
                            </select>
                            <input
                            name="perimeter"
                            type="number"
                            step="0.0001"
                            placeholder="Perimeter"
                            readOnly=""
                            value={formDataPerimeter.perimeter}
                            onChange={handleInputChange}
                            />
                        </div>
                        {/* <label className="label text-center fw-bold">Additional Materials</label>
                        <div className="form-row my-2">
                            <select 
                                name="additional_material_name"
                                value={formDataPerimeter.additional_material_name}
                                onChange={handleInputChange}
                            >
                            <option value="" hidden="">
                               -- Select Material --
                            </option>
                            {wallMaterials.map((material) => (
                                <option key={material.id} value={JSON.stringify(material)}>
                                    {material.name}
                                </option>
                            ))}
                            </select>
                            <input
                                type="number"
                                step="0.0001"
                                placeholder="Quantity per LFt"
                                name="additional_material_qty"
                                value={formDataPerimeter.additional_material_qty}
                                onChange={handleInputChange}
                            />
                        </div> */}
                         <label className="label text-center fw-bold">Additional Materials</label>
                      <div className="form-row my-2" key={0}>
                        <select
                            name={`additional_material[0]`}
                            value={additionalMaterials[0]}
                            onChange={handleInputChange}
                        >
                            <option value="" hidden>
                            -- Select Material --
                            </option>
                            {wallMaterials.map((material) => (
                            <option key={material.id} value={JSON.stringify(material)}>
                                {material.name}
                            </option>
                            ))}
                        </select>
                        <input
                                type="number"
                                step="0.0001"
                                placeholder="Quantity per LFt"
                                name={`additional_material_qty[${0}]`}
                                // name="additional_material_qty[0]"
                                value={formDataPerimeter.additional_material_qty[0]}
                                onChange={handleInputChange}
                        />
                      </div>
                      {additionalMaterials.slice(1).map((material, index) => (
                      <div className="form-row my-2" key={index + 1}>
                          <select
                            name={`additional_material[${index + 1}]`}
                            value={material}
                            onChange={handleInputChange}
                          >
                          <option value="" hidden>
                                -- Select Material --
                          </option>
                          {wallMaterials.map((material) => (
                              <option key={material.id} value={JSON.stringify(material)}>
                                {material.name}
                              </option>
                            ))}
                          </select>
                          <input
                                type="number"
                                step="0.0001"
                                placeholder="Quantity per LFt"
                                name={`additional_material_qty[${index + 1}]`}
                                value={formDataPerimeter.additional_material_qty[index + 1]}
                                onChange={handleInputChange}
                            />
                            {additionalMaterials.length > 1 && (
                            <button className="btn btn-danger mr-2" type="button" onClick={handleDeleteClick}>
                                <i className="fa-solid fa-trash" />
                            </button>
                            )}
                        </div>
                        ))}
                        <button className="btn btn-primary mr-2" type="button" onClick={handleAddClick}>
                        Add
                        </button>
                        <label className="label">Notes</label>
                        <textarea 
                            name="note" 
                            rows={10} 
                            value={formDataPerimeter.note}
                            onChange={handleInputChange}
                        />
                        <button className="btn btn-secondary" type="submit">
                            Start
                        </button>
                        </form>

                </div>
            </Draggable>, document.querySelector('#root'))
    )
}

export default PerimeterPopup;