
import { createPortal } from "react-dom";
import Draggable from "react-draggable";
import { db } from "../database/db";
import { useState } from "react";
import { useEffect } from "react";
import { toast } from "react-toastify";
import { routes } from "../Constants";
import "../Css/course.css";


const initialValues = {
    name: "",
    band_height: "",
    bottom_elevation: "",
    top_elevation: "",
    band_material: "",
    select_wall: "",
    fill_material: "",
    rebar_material: "",
    rebar_overlap: "",
    total_per_each: "",
    rebar_quantity: "",
    sq_grouted_per_cy: "",
    detail: "",
    
    total_course: "0",
    total_material_unit: "",
    total_rebar_length: "",
    total_rebar_lf: "0",
    rebar_lf_per_ton: "0",
    area_grouted_sq: "",
    total_grout_cy: "",
    total_area_grout_sq: "",
}
export default function CoursePopup({ pop, setDisplay, materials, wallHeight }) {
    // console.log(materials)
    const [wallMaterials, setWallMaterials] = useState([]);
    const [formData, setFormData] = useState({...initialValues})
    const handleCreate = async (e) => {
        e.preventDefault()
        // console.log(formData)
        await db.course_band.put(formData)
        setFormData(initialValues)
        setDisplay(false);
         toast.success('Band Saved Successfully!', { ...toastOptions });
    }
    const toastOptions = {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
    };
    // const handleInputChange = (event) => {
    //     const { name, value } = event.target;
    //      setFormData({
    //         ...formData,
    //         [name]: value,
    //       });
    // };
 useEffect(() => {
     const fetchWallMaterials = async () => {
            try {
              const response = await fetch('/api/materials', {
                method: 'GET',
                headers: {
                  Authorization: `Bearer ${routes.__token}`,
                },
              });
      
              if (!response.ok) {
                // Handle error if the response is not OK (status code 2xx)
                // Example: console.log('Error fetching wall materials data');
                return;
              }
      
              const data = await response.json();
              setWallMaterials(data);
            } catch (error) {
              // Handle error if there's an issue with the API call
              // Example: console.error('API call failed:', error);
            }
          };
          fetchWallMaterials();
      }, []);
    // console.log(materials)
    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            top_elevation: (
                (parseFloat(formData.band_height) || 0) +
                (parseFloat(formData.bottom_elevation) || 0) ||
                0
            ).toFixed(3),
        }))
    }, [
        formData.band_height,
        formData.bottom_elevation
    ])
    // console.log('check band material height', formData.band_material.length);
    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            total_per_each: (
                (parseFloat(formData.rebar_overlap) || 0) +
                (parseFloat(formData.band_material.length / 12) || 0) ||
                0
            ).toFixed(3),
        }))
    }, [
        formData.rebar_overlap,
        formData.band_material.length
    ])

    return (
        createPortal(
            <Draggable>
                <div className="modal" style={{ display: pop ? 'unset' : 'none' }}>
                    <div className="modal-header">
                        Create Course
                        <span className="close-btn" onClick={() => setDisplay(false)}>&times;</span>
                    </div>
                    <form className="modal-body" onSubmit={handleCreate}>
                        <div className="course-input">
                            <label className="label">Name</label>
                            <input
                                className="border-blue"
                                type="text"
                                name="name"
                                value={formData.name}
                                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">Band Height</label>
                            <input
                                className="border-blue"
                                type="number"
                                value={formData.band_height}
                                onChange={(e) => setFormData({ ...formData, band_height: e.target.value })}
                                name="band_height" />
                        </div>
                        <div className="course-input">
                            <label className="label">Bottom Elevation</label>
                            <input
                                className="border-blue"
                                type="number"
                                name="bottom_elevation"
                                value={formData.bottom_elevation}
                                onChange={(e) => setFormData({ ...formData, bottom_elevation: e.target.value })}
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">Top Elevation</label>
                            <input
                                type="number"
                                name="top_elevation"
                                value={formData.top_elevation}
                                onChange={(e) => setFormData({ ...formData, top_elevation: e.target.value })}
                                readOnly
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">
                                Band Material
                            </label>
                            <select
                                className="border-blue"
                                onChange={(e) => setFormData({ ...formData, band_material: wallMaterials[e.target.value] })}
                                name="band_material">
                                <option value="" hidden>-- Select --</option>
                                {wallMaterials?.map((item, index) => {
                                    return (
                                        <option key={index} value={index}>{item.name}</option>
                                    )
                                })}
                            </select>
                        </div>
                        <div className="course-input">
                            <label className="label">Fill Material</label>
                            <select
                                className="border-blue"
                                onChange={(e) => setFormData({ ...formData, fill_material: wallMaterials[e.target.value] })}
                                name="fill_material">
                                <option value="" hidden>-- Select --</option>
                                {wallMaterials?.map((item, index) => {
                                    return (
                                        <option key={index} value={index}>{item.name}</option>
                                    )
                                })}
                            </select>
                        </div>
                        <div className="course-input">
                            <label
                                className="label"
                            >
                                Rebar Material
                            </label>
                            <select
                                className="border-blue"
                                onChange={(e) => setFormData({ ...formData, rebar_material: wallMaterials[e.target.value] })}
                                name="rebar_material">
                                <option value="" hidden>-- Select --</option>
                                {wallMaterials?.map((item, index) => {
                                    return (
                                        <option key={index} value={index}>{item.name}</option>
                                    )
                                })}
                            </select>
                        </div>
                        <div className="course-input">
                            <label className="label">Rebar Overlap</label>
                            <input
                                className="border-blue"
                                type="number"
                                value={formData.rebar_overlap}
                                onChange={(e) => setFormData({ ...formData, rebar_overlap: e.target.value })}
                                name="rebar_overlap" />
                        </div>
                        <div className="course-input">
                            <label className="label">Total per each</label>
                            <input
                                type="number"
                                name="total_per_each"
                                value={formData.total_per_each}
                                readOnly
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">Rebar Quantity</label>
                            <input
                                className="border-blue"
                                type="number"
                                value={formData.rebar_quantity}
                                onChange={(e) => setFormData({ ...formData, rebar_quantity: e.target.value })}
                                name="rebar_quantity"
                            />
                        </div>
                        <div className="course-input">
                        <label className="label">Sq Grouted per cy </label>
                        <input
                            className="border-blue"
                            name="sq_grouted_per_cy"
                            type={'number'}
                            value={formData.sq_grouted_per_cy}
                                onChange={(e) => setFormData({ ...formData, sq_grouted_per_cy: e.target.value })}
                        />
                        </div>
                        <div className="course-input">
                            <label className="label">
                                Select Wall
                            </label>
                            <select
                                className="border-blue"
                                onChange={(e) => setFormData({ ...formData, select_wall: e.target.value })}
                                name="select_wall">
                                <option value="" hidden>-- Select --</option>
                                <option value="main">Main</option>
                                 {/* <option value="top">Top</option>
                                <option value="bottom">Bottom</option>
                                <option value="top_botton">Top Bottom</option> */}
                            </select>
                        </div>
                        <div className="course-input-textarea">
                            <label className="label">Details</label>
                            <textarea 
                                name="detail" 
                                rows={5} 
                                onChange={(e) => setFormData({ ...formData, detail: e.target.value })}
                            /> 
                        </div>
                        <button type="submit" className="btn btn-primary mt-2">Create</button>
                    </form>
                </div>
            </Draggable>,
            document.querySelector('#root')
        )
    )
}